body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
      monospace;
  }
  *{
    margin: 0%;
    padding:  0%;
    box-sizing: border-box;
  }
  
  .main_div{
       width:  100vw;
       height: 100vh;
       display: flex;
       align-items: center;
       justify-content: center;
       
  }
  .center_div{
    width: 100%;
    height: 100%;
    text-align: center;
    border-radius: 20px;
    background-color: #f0f8ff;
    display: block;
    overflow-y: auto;
    box-shadow: 5px 5px 15px -5px rgb(10 10 10 / 90%);
    position: relative;
    background-color: transparent;
       
  }
  .center_div::-webkit-scrollbar {
    width: 1em;
    display: none;
    
  }
   
  .center_div::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  }
   
  .center_div::-webkit-scrollbar-thumb {
    background-color: darkgrey;
    outline: 1px solid slategrey;
  }
  .center_div h1{
    border: 1px solid black;
    background-color: purple;
    position: -webkit-sticky;
    position: sticky;
    top: 0%;
    /* background-color: #eee; */
    
  }
  .alag input{
    text-align: center;
    height: 30px;
    top: 10px;
    width: 60%;
    border: none;
    background: transparent;
    font-size: 25px;
    font-weight: 500;
    border-bottom: 2px solid purple;
    outline: none;
    color: white;
  }
  .alag input:-ms-input-placeholder{
      font-size: 17px;
  }
  .alag input:placeholder-shown{
      font-size: 17px;
  }
  .alag button{
    border: none;
    min-height: 40px;
    width: 40px;
    border-radius: 50%;
    border-color: transparent;
    font-size: 30px;
    outline: none;
    background-color: #8566aa;
    left: 10px;
    cursor: pointer;
    box-shadow: 5px 5px 15px -5px rgba(0, 0, 0,0.3);
  }
 .alag button:hover{
    background-color: rgb(64, 207, 64);
  }
  
  ol{
    
    /* margin-left: 10px; */
    display: flex;
    flex-direction: column;
    width: 80%;
    margin: auto;
    margin-top: 25px;
  }
  
  
  
  .todo-style{
    display: flex;
    
    align-items: center;
    margin: 5px 5px;
    padding: 5px 15px;
    border: 1px solid white;
    border-radius: 50px;
    box-shadow: 5px 5px 5px -5px rgb(0 0 0 / 70%);
    background-color: white;
  }
  ol li{
    list-style: none;
    text-align: left;
    font-size: 20px;
    color: #8566aa;
    font-weight: 500;
    min-height: 40px;
    display: flex;
    align-items: center;
    padding-left: 5px;
    background-color: white;
  }
  .todo-style i{
     height: 16px;
    width: 18px;
    border-radius: 50%;
    border-color: transparent;
    font-size: 20px;
    outline: none;
    background-color: black;
    display: flex;
    align-items: center;
    justify-content: center;
    left: 10px;
    cursor: pointer;
    box-shadow: 5px 5px 15px -5px rgb(0 0 0 / 30%);
    color: white;
    margin: 0px 10px;
  }
  .todo-style i:hover{
    background-color: white;
    color: tomato;
  }
  
